import React, { Component } from 'react';
import styled from 'styled-components';
import axios from 'axios';

const Form = styled.form`
  margin-bottom: 15px;
  input, textarea {
    display: block;
    margin: 5px 0;
    line-height: 34px;
    width: 390px;
    border: 1px solid silver;
    padding-left: 5px;
  }
`

const Button = styled.button`
  display: block;
  padding: 10px;
  border-radius: 4px;
  color: gray;
`

const InquirySubmitted = styled.p`
  padding-bottom: 10px;
  color: #3498db !important;
`

const encode = data =>
  Object.keys(data)
    .map(key => `${encodeURIComponent(key)}=${encodeURIComponent(data[key])}`)
    .join('&')

export default class extends Component {
  state = {
    submitted: false
  }
  submitForm = async e => {
    console.log('submitted')
    console.log(e)
    e.preventDefault()
    try {
      await axios.post('/', encode({ 'form-name': 'inquiry', ...e }), {
        headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
      })
      this.setState({ submitted: true })
    } catch (err) {
      console.log(err)
      window.alert(
        'There was a problem submitting your form! Try again or reload the page :)',
      )
      this.setState({ submitted: true })
    }    
  }
  render() {
    if (this.state.submitted) {
      return (<InquirySubmitted>Your inquiry has been submitted. We'll be in contact with you as soon as possible</InquirySubmitted>)
    } else {
      return (
        <Form name="inquiry" method="post" data-netlify="true" data-netlify-honeypot="bot-field" onSubmit={this.submitForm}>
          <input type="hidden" name="form-name" value="inquiry" disabled />
          <input type="text" name="name" placeholder="Name"  disabled/>
          <input type="tel" name="tel" placeholder="Contact number"  disabled/>
          <input type="email" name="email" placeholder="Email Address"  disabled/>
          <textarea placeholder="Other remarks" style={{ minHeight: '150px' }} disabled></textarea>
          <Button type="submit" disabled>Submit</Button>
        </Form>
      )
    }
  }
}

